/* Styles généraux */

body {
    font-family: Arial, sans-serif;
    background-color: #f4f4f4;
    color: #333;
    margin: 0;
    padding: 0;
}

.web3d-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
}


/* Titre du module avec un fond coloré */

.header-module {
    background-color: #001f3f;
    color: #fff;
    padding: 20px;
    text-align: center;
    margin-bottom: 20px;
    width: 100%;
    /* Utiliser 100% pour que ça prenne toute la largeur */
}

.header-module h1 {
    margin: 0;
}


/* Titre de chaque TP */

.web3d-container h2 {
    font-size: 1.5rem;
    color: #333;
    margin-bottom: 10px;
}


/* Texte descriptif */

.web3d-container p {
    font-size: 1rem;
    line-height: 1.5;
    margin-bottom: 20px;
}


/* Carousel de la galerie d'images */

.carousel-container {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.image-frame {
    position: relative;
    max-width: 1400px;
    padding: 15px;
    border: 2px solid #001f3f;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #f0f0f0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.tp-image {
    width: 100%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}


/* Boutons de téléchargement et de navigation */

.download-button {
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #001f3f;
    color: #fff;
    border: none;
    padding: 8px 12px;
    border-radius: 4px;
    font-size: 0.9em;
    cursor: pointer;
    transition: background-color 0.3s;
}

.download-button:hover {
    background-color: #21867a;
}

.nav-button {
    font-size: 2em;
    background: none;
    border: none;
    color: #001f3f;
    cursor: pointer;
    transition: color 0.2s;
    padding: 0 10px;
    /* Ajout de padding pour les boutons de navigation */
}

.nav-button:hover {
    color: #0056b3;
    /* Changé pour une meilleure visibilité au survol */
}


/* Container pour la visualisation du modèle 3D */

.three-container {
    text-align: center;
    margin-top: 20px;
    width: 100%;
    /* Utilisez 100% de la largeur disponible */
    max-width: 800px;
    /* Ajustez cette valeur selon vos besoins */
    margin-left: auto;
    /* Centrez le conteneur horizontalement */
    margin-right: auto;
    /* Centrez le conteneur horizontalement */
    height: 1000px;
}

.three-container h2 {
    margin-bottom: 10px;
}

.three-canvas {
    width: 100%;
    height: 2000px;
    padding: 40px;
    /* Augmentez cette valeur pour une plus grande hauteur */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}


/* Titre des TP */

.tp-container h2 {
    color: #000;
    font-size: 1.6em;
    margin-top: 20px;
}